













import { Vue, Component, Prop, Emit } from 'vue-property-decorator';
import api from '../api/network';

@Component({})
export default class UploadImg extends Vue {

    /** 上传的图片路径 */
    @Prop({
        type: String,
        default: ''
    })
    private imgUrl!: string;

    /** 发送数据到父组件中 */
    @Emit('getImgUrl') sendImgSrc(src: string) {}

    /** 上传图片 */
    private uploadImg() {
        let input: any = this.$refs.uploadinput;
        let file: File = input.files[0];
        // console.log('上传图片文件 >>', file);
        
        /** 上传类型数组 */
        let typeStr = ['image/jpg', 'image/png', 'image/jpeg', 'image/gif'];

        // 判断文件类型
        if (typeStr.indexOf(file.type) < 0) {
            // Global.openDialog({
            //     type: 'alert',
            //     content: '文件格式只支持：jpg 和 png'
            // });
            return;
        }

        // 判断大小
        if (file.size > 2 * 1024 * 1024) {
            // Global.openDialog({
            //     type: 'alert',
            //     content: '上传的文件不能大于2M'
            // });
            return;
        }

        // 静态上传
        const src = this.getObjectURL(file);
        this.sendImgSrc(src);
        input.value = null;
        
        // let formData = new FormData();
        // formData.append('img', file);
        
        // Global.openDialog({ type: 'loading', content: '图片上传中' });
        // api.uploadImg(formData, res => {
        //     Global.hideDialog();
        //     // console.log('上传成功 >>', res);
        //     /** 图片路径 */
        //     let src: string = res.data[0];
        //     this.sendImgSrc(src);
        // }, err => {
        //     Global.hideDialog();
        //     console.log('上传图片失败 >>', err);
        //     setTimeout(() => {
        //         Global.openDialog({
        //             type: 'alert',
        //             content: '上传图片失败'
        //         });
        //     }, 10);
        // });
    }

    /** 清除当前图片 */
    private removeImg() {
        this.sendImgSrc('');
    }

    /**
     * 获取二进制路径（需要打开服务器调试）
     * @param file 文件
     */
    private getObjectURL(file: File) {
        let url = null;
        // if (window['createObjectURL']) {
        //     url = window['createObjectURL'](file);
        // } else if (window.URL) {
        //     url = window.URL.createObjectURL(file);
        // } else if (window['webkitURL']) {
        //     url = window['webkitURL'].createObjectURL(file);
        // }
        url = window.URL.createObjectURL(file);
        return url;
    }
}
